body, ul, li, button {
    margin: 0;
    padding: 0;
}

body {
    font: 12px/1.5 Tahoma;
}

ul {
    list-style-type: none;
}

button {
    cursor: pointer;
}

#outer {
    width: 70px;
    margin: 10px auto;
}

#ime {
    margin-top: 5px;
    background: #fff;
    border: 1px solid #9a99ff;
    display: none;
}
#ime.new{
    display: block;
}
    #ime li {
        width: 100%;
        line-height: 24px;
        display: inline-block;
        vertical-align: top;
    }

#close {
    border-top: 1px solid #9a99ff;
}

#ime li a {
    color: #00c;
    padding: 0 5px;
    display: block;
    text-decoration: none;
}

    #ime li a:hover {
        background: #d9e1f6;
    }
